<template>
  <tablepage
    :showData="showData"
    @pageChange="getPlantData"
    :total="total"
  ></tablepage>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "rank",
  data() {
    return {
      test: this.$store.state.plant.total,
    };
  },
  computed: {
    ...mapState({
      showData: (state) => state.plant.pageData,
      total: (state) => state.plant.total,
    }),
  },
  methods: {
    // 获取页面数据
    getPlantData(page) {
      console.log(this.total, this.showData, "fasdfasf");
      this.$store.dispatch("plant/sendPlantRequest", { page: page });
    },
  },
  mounted() {
    this.getPlantData(1);
  },
};
</script>

